* {
  box-sizing: border-box;
}

body {
  margin: 0;
  width: 100%;
  height: 100vh;
  font-family: VogueAvantGarde, helvetica, sans-serif;
}

.header {
  font-size: 24px;
  text-align: center;
}

.container {
  width: 100%;
  max-width: 1200px;
  border-radius: 4px;
  margin: 0 auto;
  margin-bottom: 40px;
}

.content {
  display: flex;
  flex-wrap: wrap;
  padding: 0 30px;
  justify-content: center;
}

.wrapper {
  width: 33.3%;
  height: 100%;
  padding: 10px;
}

.name {
  position: relative;
  font-size: 16px;
  display: inline-block;
}

.name:after {
  content: "";
  position: absolute;
  width: calc(100% + 10px);
  height: 1px;
  background-color: #000;
  bottom: -4px;
  left: 0;
}

.box {
  position: relative;
  max-height: 300px;
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 0 1.4px 1.7px rgba(0, 0, 0, 0.017),
    0 3.3px 4px rgba(0, 0, 0, 0.024), 0 6.3px 7.5px rgba(0, 0, 0, 0.03),
    0 11.2px 13.4px rgba(0, 0, 0, 0.036), 0 20.9px 25.1px rgba(0, 0, 0, 0.043),
    0 50px 60px rgba(0, 0, 0, 0.06);
}

.box .hide {
  opacity: 0;
}

.box .frame {
  position: absolute;
  border: 1px solid #fff;
  z-index: 2;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.box h2,
.box p {
  position: absolute;
  color: #fff;
  z-index: 2;
  width: 100%;
  transition: opacity 0.2s, transform 0.3s;
}

.box h2 {
  font-weight: 500;
  font-size: 22px;
  margin-bottom: 0;
  letter-spacing: 1px;
}

.box p {
  bottom: 0;
  font-size: 14px;
  letter-spacing: 1px;
}

.box:hover {
  transition: all 0.3s ease-in-out;
}

.box:hover:before {
  transition: all 0.3s ease-in-out;
}

.box img {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  transition: all 0.3s ease-in-out;
}

.box img:hover {
  transition: all 0.3s ease-in-out;
}

.box img:after {
  content: "";
  position: absolute;
  background-color: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
}

.box img:hover:after {
  transition: all 0.3s ease-in-out;
  opacity: 1;
}

.zoom-in h2 {
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
  margin: 0;
}

.zoom-in p {
  text-align: center;
  /* top: calc(50% + 60px); */
  top: 25%;
  transition: all 0.3s ease;
  transform: scale(1.8);
  opacity: 0;
  color: black;
}

.zoom-in:hover img {
  transform: scale(1.1);
  filter: grayscale(100%);
}

.zoom-in:hover p {
  transform: scale(1);
  transition: all 0.3s ease;
  opacity: 1;
}

.w-content .frame {
  width: calc(100% - 100px);
  height: calc(100% - 100px);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-color: transparent;
}

.w-content h2 {
  margin-top: 0;
  top: 50%;
  transform: translatey(-50%);
  text-align: center;
  letter-spacing: 1px;
}

.w-content p {
  transform: translate3d(0, -10px, 0);
}

.w-content:hover .frame {
  border-color: #fff;
  transition: color 0.3s ease-in-out, all 0.3s ease-in-out;
  width: calc(100% - 20px);
  height: calc(100% - 20px);
}

.w-content:hover {
  opacity: 0.8;
}

.postcard h2 {
  top: 50%;
  text-align: left;
  transform: translate3d(50px, 20px, 0);
}

.postcard p {
  transform: translate3d(40px, 60px, 0);
  font-size: 12px;
  letter-spacing: 1px;
  color: black;
  width: 60%;
}

.postcard:hover h2 {
  transform: translate3d(20px, 20px, 0);
}

.postcard:hover p {
  opacity: 1;
}

.postcard:hover img {
  filter: opacity(0.7);
  transform: translate3d(-30px, 0, 0);
}

.blury-card .frame {
  width: calc(100% - 40px);
  height: calc(100% - 40px);
  opacity: 0;
  transition: all 0.3s ease-in-out;
}

.blury-card h2 {
  top: 5%;
  transform: translate3d(0, 60px, 0);
  text-align: center;
}

.blury-card p {
  opacity: 0;
  bottom: 0;
  transform: translate3d(0, -10px, 0);
  font-size: 14px;
  letter-spacing: 1px;
  text-align: center;
  color: black;
}

.blury-card:hover h2 {
  top: 10%;
  transform: translate3d(0, 20px, 0);
}

.blury-card:hover p {
  opacity: 1;
  transform: translate3d(0, -30px, 0);
}

.blury-card:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #ccc2ad;
  z-index: 1;
  left: 0;
  opacity: 0;
  transition: all 0.3s ease-in-out;
}

.blury-card img {
  z-index: 2;
}

.blury-card:hover .frame {
  width: calc(100% - 120px);
  height: calc(100% - 120px);
  opacity: 1;
}

.blury-card:hover img {
  opacity: 0.7;
}

.blury-card:hover:before {
  opacity: 1;
  transition: all 0.3s ease-in-out;
}

.vintage h2 {
  top: 5%;
  transform: translate3d(0, 60px, 0);
  text-align: center;
}

.vintage p {
  opacity: 0;
  bottom: 0;
  transform: translate3d(0, -10px, 0);
  font-size: 14px;
  letter-spacing: 1px;
  text-align: center;
  color: black;
}

.vintage:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 60%;
  background: linear-gradient(
    to bottom,
    rgba(198, 196, 190, 0) 0%,
    rgba(198, 196, 190, 0.8) 75%
  );
  z-index: 2;
  bottom: -100%;
  left: 0;
}

.vintage:hover:before {
  bottom: 0;
}

.vintage:hover h2 {
  top: 10%;
  transform: translate3d(0, 20px, 0);
}

.vintage:hover p {
  opacity: 1;
  transform: translate3d(0, -30px, 0);
}

.zoom-out .frame {
  width: calc(100% - 100px);
  height: calc(100% - 100px);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: calc(100% - 80px);
  height: calc(100% - 80px);
}

.zoom-out h2 {
  bottom: 50%;
  transform: translateY(-50%);
  text-align: right;
  margin: 0;
}

.zoom-out p {
  text-align: center;
  bottom: calc(50% + 40px);
  transition: all 0.3s ease;
  transform: scale(1.8);
  opacity: 0;
  color: black;
}

.zoom-out:hover p {
  transform: scale(1);
  transition: all 0.3s ease;
  opacity: 1;
}

.zoom-out img {
  transform: scale(1.1);
}

.zoom-out:hover img {
  transform: scale(1);
  filter: contrast(70%);
}

@media screen and (max-width: 880px) {
  .wrapper {
    width: 50%;
  }
}

@media screen and (max-width: 520px) {
  .wrapper {
    width: 100%;
  }
}
